.login {
    width: 90%;
    max-width: 40rem;
    padding: 2rem;
    margin: 2rem auto;
}

.login-header {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.login-header div {
    width: 100px;
    height: 100px;
}

.login-header h2 {
    color: var(--Blue);
    margin: 1rem 0;
}

.login-footer {
    margin: 1.5rem 0
}

.login-footer a {
    color: var(--Blue);
    text-decoration: none;
}

.login-footer a:hover {
    color: var(--LighterBlue);
}

.user-password button:hover {
    color: var(--LighterBlue);
}

.user-password {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
}

.user-password button {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    background: transparent;
    border: none;
    cursor: pointer;
    color: var(--ShadowDarkBlue);
    padding: 0.35rem 0.7rem 0.35rem 0.35rem;
}
